@forward "./global";
@use "./abstracts/" as *;

.cover {
  height: 573px;
  width: 100%;

  &__container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 45px 0;

    @media (max-width: $mobileWidth) {
      flex-direction: column-reverse;
    }

    &__content {
      width: 619px;

      @media (max-width: $mobileWidth) {
        text-align: center;
        width: 100%;
      }

      &__title {
        line-height: 66.5px;

        @media (max-width: 1050px) {
          font-size: 33px !important;
          line-height: normal;
          margin-top: 24px;
        }

        @media (max-width: $mobileWidth) {
          font-size: 28px !important;
        }
      }

      &__text {
        color: #fff !important;
        line-height: 28px;
      }
    }

    .romb__img {
      transition: 300ms ease-in-out;

      @media (max-width: 1050px) {
        width: 300px;
        height: 300px;
      }

      @media (max-width: $mobileWidth) {
        width: 180px;
        height: 180px;
      }

      &:hover {
        transform: rotate(180deg);
      }
    }
  }
}

.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 60px;

  &__content {
    margin: 48px 0;
    width: 800px;
    text-align: center;

    @media (max-width: $mobileWidth) {
      width: 100%;
    }
  }

  &__faq {
    width: 100%;
    padding: 62px 0;
    position: relative;

    &:hover {
      .romb__img__left {
        left: 50%;
        animation: exp;
        animation-duration: 6000ms;
        animation-timing-function: ease;
        animation-delay: 6000;
        animation-iteration-count: infinite;
        animation-play-state: running;
        animation-fill-mode: both;
      }

      @keyframes exp {
        0% {
          filter: drop-shadow(0 0 white);
        }

        25% {
          filter: drop-shadow(0 0 white);
        }

        75% {
          filter: drop-shadow(7px -7px white);
        }

        100% {
          filter: drop-shadow(0 0 white);
        }
      }

      .romb__img__right {
        right: 50%;
        animation: exp;
        animation-duration: 6000ms;
        animation-timing-function: ease;
        animation-delay: 6000;
        animation-iteration-count: infinite;
        animation-fill-mode: forwards;
        animation-play-state: running;
      }
    }

    .romb__img__left {
      transition: 200ms ease-in-out;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);

      @media (max-width: $mobileWidth) {
        display: none;
      }
    }

    .romb__img__right {
      transition: 200ms ease-in-out;

      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);

      @media (max-width: $mobileWidth) {
        display: none;
      }
    }

    &__container {
      display: flex;
      justify-content: space-between;

      @media (max-width: $mobileWidth) {
        flex-direction: column;
      }

      &__left {
        width: 530px;

        @media (max-width: $mobileWidth) {
          width: 100%;
        }

        &__title {
          color: #fff !important;
          line-height: 35px;
        }

        &__text {
          line-height: 28px;
          color: #fff !important;
          margin: 0;
        }
      }

      &__right {
        width: 530px;

        @media (max-width: $mobileWidth) {
          width: 100%;
          margin-top: 48px;
        }

        &__title {
          color: #fff !important;
          line-height: 35px;
        }

        &__text {
          line-height: 28px;
          color: #fff !important;
          margin: 0;
        }
      }
    }
  }
}

.branches {
  &__container {
    &__header {
      display: flex;
      justify-content: center;
      margin-bottom: 32px;

      &__content {
        width: 800px;
        text-align: center;

        h2 {
          line-height: 35px;
        }

        p {
          list-style: 28px;
        }
      }
    }

    &__ul {
      margin-bottom: 48px;

      &__li {
        list-style: none;
        border-top: 1px solid #e3e3e3;
        display: flex;
        height: 292px;
        justify-content: space-between;

        @media (max-width: $mobileWidth) {
          flex-direction: column-reverse;
          height: auto;
        }

        &__image {
          height: 100%;
          width: 469px;
          position: relative;

          @media (max-width: $mobileWidth) {
            height: 233.48px;
            width: 100%;

            margin: 24px 0;

          }

          &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: linear-gradient(90deg,
                #005e48 -42.86%,
                rgba(0, 0, 0, 0) 100%);
          }

          img {
            height: 100%;
            width: 100%;
            object-fit: cover;

            @media (max-width: $mobileWidth) {
              height: 233.48px;

            }
          }
        }

        &__content {
          display: flex;
          flex-direction: column;
          justify-content: center;
        }

        &:last-child {
          border-bottom: 1px solid #e3e3e3;
        }

        h3 {
          line-height: 35px;
        }

        p {
          @media (max-width: $mobileWidth) {
            margin-bottom: 2px;
          }
        }
      }
    }
  }
}

.footer {
  padding: 48px 0;

  &__container {
    display: flex;
    justify-content: space-between;

    @media (max-width: $mobileWidth) {
      flex-direction: column-reverse;
      align-items: center;
    }

    &__map {
      border-radius: 20px;
      width: 563px;
      height: 274px;
      overflow: hidden;

      @media (max-width: $mobileWidth) {
        margin-bottom: 20px;
        width: 100%;
      }
    }

    &__content {
      @media (max-width: $mobileWidth) {
        width: 100% !important;
      }

      &__logo {
        margin-bottom: 24px;

        @media (max-width: $mobileWidth) {
          display: none;
        }
      }

      &__logo2 {
        margin-bottom: 24px;
        display: none;

        @media (max-width: $mobileWidth) {
          display: block;
        }
      }
    }
  }
}

.copyright {
  background: #00513e;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: 200;
  text-transform: lowercase;
  white-space: 10px;
}